import React, { Fragment, useEffect, useState } from "react";
import ReactDOM from "react-dom";
import brew from "brew-js";
import { always, either, extend, noop } from "./include/zeta-dom/util.js";
import { containsOrEquals, removeNode } from "./include/zeta-dom/domUtil.js";
import dom from "./include/zeta-dom/dom.js";

export function createDialog(props) {
    var root = document.createElement('div');
    var closing = false;
    var refresh = noop;
    var promise;

    function Component() {
        const forceRefresh = useState(0)[1];
        refresh = function () {
            forceRefresh(function (v) {
                return ++v;
            });
        };
        return props.onRender(extend({ root, closeDialog }, props));
    }

    function closeDialog(value) {
        if (!closing) {
            closing = true;
            brew.closeFlyout(root, value).then(function () {
                closing = false;
                removeNode(root);
                ReactDOM.unmountComponentAtNode(root);
                (props.onClose || noop)(root);
            });
        }
    }

    return {
        root: root,
        refresh: refresh,
        close: closeDialog,
        open: function () {
            if (promise) {
                return promise;
            }
            root.className = props.className;
            if (props.modal) {
                root.setAttribute('is-modal', '');
            }
            document.body.appendChild(root);
            if (props.modal) {
                dom.setModal(root);
            }
            dom.retainFocus(dom.activeElement, root);
            ReactDOM.render(React.createElement(Component), root);

            promise = brew.openFlyout(root);
            always(promise, function () {
                promise = null;
            });
            promise.then(props.onCommit);
            (props.onOpen || noop)(root);
            return promise;
        }
    };
}

export function Dialog(props) {
    /** @type {import("./dialog").DialogProps & { open: boolean }} */
    const _props = useState({ open: false })[0];
    extend(_props, props);
    if (props.children) {
        _props.onRender = function () {
            return React.createElement(Fragment, null, props.children);
        };
    }
    const dialog = useState(function () {
        return createDialog(_props);
    })[0];

    useEffect(function () {
        var opened = containsOrEquals(dom.root, dialog.root);
        if (either(opened, _props.open)) {
            if (!opened) {
                dialog.open();
            } else {
                dialog.close();
            }
        }
    }, [_props.open])
    useEffect(function () {
        return dialog.close;
    }, [dialog]);
    useEffect(function () {
        dialog.refresh();
    });
    return React.createElement(Fragment);
}
